import React from "react"
import { ThemeToggler } from 'gatsby-plugin-dark-mode'
import "../styles/switch.css"

export default function Home() {
  return (
    <ThemeToggler>
        {({ theme, toggleTheme }) => (
          <label className="switch"><input type="checkbox" onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
          checked={theme === 'dark'}/><div></div>
          </label>
        )}
      </ThemeToggler>
  )
}
